:root {
  --bg-primary: #282c34;
  --clr-1: purple;
  --clr-2: rgb(4, 0, 128);
  --clr-3: rgb(64, 0, 128);
}

.App {
  text-align: center;
  width: min(95%, 1200px);
  margin-inline: auto;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

body {
  background-color: var(--bg-primary);
  min-height: 100vh;
  display: flex;
  gap: 1em;
  flex-direction: column;
  font-size: 1rem;
  color: white;
}

.App-header {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

nav {
  display: flex;
  align-self: stretch;
  align-items: center;
  justify-content: space-between;
}

h1 {
  max-width: 10ch;
}

.primary-nav {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  gap: 5rem;
}

.primary-nav * {
  list-style: none;
  text-decoration: none;
  color: currentColor;
}

input {
  padding: 1em;
  border-radius: .4em;
}

.grid {
  display: grid;
  gap: 1rem;
  background-color: #61dafb54;
}

.flex {
  display: flex;
}

.dir-col {
  flex-direction: column;
}

.aln-cntr {
  align-items: center;
}
