img {
  object-fit: cover;
  display: block;
  max-width: 100%;
}

main {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-top: 4rem;
}

.leftSidebar {
  text-align: left;
  padding: 0;
}

