/* @keyframes sidebarLoading {
  0% {background-size: 100% 0%;}
  50% {background-size: 100% 100%;}
  100% {background-size: 100% -100%;}
} */

.leftSidebar> *+* {
  margin-top: 2em;
}

.leftSidebar li:nth-child(1) {--space: 2.5rem}
.leftSidebar li:nth-child(2) {--space: 2rem}
.leftSidebar li:nth-child(3) {--space: 1.5rem}
.leftSidebar li:nth-child(4) {--space: 1rem}
.leftSidebar li:nth-child(5) {--space: .3rem}
.leftSidebar li:nth-child(6) {--space: 0rem}

.leftSidebar li {
  list-style: none;
  position: relative;

  font-size: .8em;
  font-style: italic;

  color: var(--bg-primary);
  max-width: 250px;
  margin-left: var(--space);
  padding: .5em 2em;
  border-radius: 2em 1em 2em 1em;
}

[class*="Sidebar"] li::before,
[class*="Sidebar"] li::after {
  content: '';
  inset: 0;
  position: absolute;
  z-index: -1;
  transform: skew(-20deg);
  background-image:linear-gradient(var(--_gradient-color),var(--_gradient-color));
  background-repeat: no-repeat;
}
[class*="Sidebar"] li::after {
  --_gradient-color: #61dafb;
}
[class*="Sidebar"] li::before {  
  translate: .3em .5em;
  --_gradient-color: #616bfb;
}

[class*="Sidebar"].loading li::before,
[class*="Sidebar"].loading li::after{
  animation: popIn 1000ms;
}

[class*="Sidebar"] li>span {
  font-size: 1rem;
}

@keyframes popIn {
  0% {   background-size: 0;}
 100% { background-size: 100%;}
}
